import React, {useState} from "react";
import * as Yup from "yup";
import {useFormik} from "formik";
import {PatternFormat} from "react-number-format";
import ym from "react-yandex-metrika";

export const Form = () => {
    const phoneRegExp = /^[0-9]{1}\s\([0-9]{3}\)\s[0-9]{3}\s[0-9]{2}-[0-9]{2}$/
    const [sent, setSent] = useState(false)
    const sendMetrika = () => {
        ym('hit');
        ym('reachGoal', 'MAIN_FORM');
    }

    const formik = useFormik({
        initialValues: {
            username: '',
            phone: '',
            useremail: '',
        },
        validationSchema: Yup.object().shape({
            username: Yup.string().trim().required(',  ').min(3, '  ').max(35, '  '),
            phone: Yup.string().trim().required(' ').matches(phoneRegExp, '  '),
            useremail: Yup.string().trim().required(' ').email('  '),
        }),
        onSubmit: (values, {resetForm}) => {
            const {username, phone, useremail} = values
            // @ts-ignore
                Email.send({
                    Host: "smtp.elasticemail.com",
                    Username: "sales@lider-stk.com",
                    Password: "EDB5A24ED6F812D1FB9B8FAF76E68BA37B14",
                    To: 'sales@lider-stk.com',
                    From: "sales@lider-stk.com",
                    Subject: "   |    ",
                    Body: `: ${username}<br/>: ${phone}<br/>: ${useremail}`
                });
            resetForm({values: {username: '', phone: '', useremail: ''}})
            setSent(true)
            sendMetrika()
        },
    })



    return (
        <form className='form' method="get" onSubmit={formik.handleSubmit}>

            <input className='formInput callbackwidget-name' placeholder={' '}
                   type="text"
                   {...formik.getFieldProps('username')}
            />
            <PatternFormat className='formInput' placeholder={' '}
                           format="# (###) ### ##-##"
                           patternChar="#"
                           mask="_"
                           {...formik.getFieldProps('phone')}
            />
            {formik.errors.username && formik.touched.username && (
                <div className='inputError'>{`${formik.errors.username}`}</div>
            )}
            {formik.errors.phone && formik.touched.phone && (
                <div className='inputError'>{`${formik.errors.phone}`}</div>
            )}
            <input className='formInput callbackwidget-email' placeholder={' '}
                   type="email"
                   style={{width: '100%', marginBottom: 0}}
                   {...formik.getFieldProps('useremail')}
            />
            {formik.errors.useremail && formik.touched.useremail && (
                <div className='inputError'>{`${formik.errors.useremail}`}</div>
            )}

            <button type={'submit'} >  </button>

            {sent && <p className={'sent'}>    ! ,  .</p>}

            <div className={'agreement'}>
                <div style={{display: "flex", alignItems: 'center'} }>
                    <input type={'checkbox'} defaultChecked={true}/>
                    <p>    </p>
                </div>
                <p style={{fontSize: '16px'}}>  15        </p>
            </div>

        </form>
    )
}